package dev.oniksen.demotelegramminiapp

import androidx.compose.foundation.layout.Column
import androidx.compose.material.Button
import androidx.compose.material.MaterialTheme
import androidx.compose.material.Text
import androidx.compose.runtime.*

@Composable
fun QrScannerContent(webApp: TelegramMiniApp) {
    MaterialTheme {
        Column {
            var qrText by remember { mutableStateOf<String?>(null) }

            Text("Сканировать QR (версия: ${webApp.version})")
            Text("Текст на коде: $qrText")

            Button(
                onClick = {
                    webApp.showScanQrPopup("Наведите камеру на QR-код") { state ->
                        qrText = state
                        true
                    }
                }
            ) {
                Text("Test")
            }
        }
    }
}